@forward '../markdown/partial-styles/_kbd.scss';
:host(limel-hotkey) {
    display: flex;
    align-items: center;
    justify-content: center;
}

kbd {
    display: inline-block;
    font-size: 0.75rem;
    padding: 0.0625rem 0.375rem;

    &::first-letter {
        text-transform: uppercase;
    }
}
