import {
    Component,
    Event,
    EventEmitter,
    Host,
    Listen,
    Prop,
    h,
} from '@stencil/core';
import {
    hotkeyFromKeyboardEvent,
    isKeyboardEventFromTextInput,
    normalizeHotkeyString,
    tokenizeHotkeyString,
} from '../../util/hotkeys';
import { isAppleDevice } from '../../util/device';

/**
 * This component is used internally to visualize hotkeys in other components,
 * such as the menu. It will also emit an event when the hotkey is pressed,
 * so that the parent component can react to it.
 *
 * @exampleComponent limel-example-hotkey-basic
 * @private
 */
@Component({
    tag: 'limel-hotkey',
    shadow: true,
    styleUrl: 'hotkey.scss',
})
export class Hotkey {
    /**
     * The hotkey
     */
    @Prop({ reflect: true })
    public value: string;

    /**
     * Emits when the configured hotkey is pressed.
     */
    @Event({ bubbles: true, composed: true })
    public hotkeyTrigger: EventEmitter<{
        hotkey: string;
        value: string;
        keyboardEvent: KeyboardEvent;
    }>;

    private static readonly handledFlag = '__limelHotkeyHandled';

    public render() {
        const isApple = isAppleDevice();
        const parts = tokenizeHotkeyString(this.value);
        return (
            <Host>
                {parts.map((part) => (
                    <kbd>{this.formatDisplayToken(part, isApple)}</kbd>
                ))}
            </Host>
        );
    }

    private formatDisplayToken(token: string, isApple: boolean): string {
        const trimmed = (token ?? '').trim();
        if (!trimmed) {
            return '';
        }

        if (trimmed === '+') {
            return '+';
        }

        const lower = trimmed.toLowerCase();

        switch (lower) {
            case 'meta':
            case 'cmd':
            case 'command': {
                return isApple ? '⌘' : 'Ctrl';
            }

            case 'alt':
            case 'option': {
                return isApple ? '⌥' : 'Alt';
            }

            case 'shift': {
                return '⇧';
            }

            case 'enter':
            case 'return': {
                return '↩';
            }

            case 'delete':
            case 'del':
            case 'backspace': {
                if (isApple) {
                    return '⌫';
                }
                return lower === 'backspace' ? 'Backspace' : 'Del';
            }

            case 'ctrl':
            case 'control': {
                return 'Ctrl';
            }
        }

        return trimmed;
    }

    @Listen('keydown', { target: 'document', capture: true })
    public handleKeyDown(event: KeyboardEvent) {
        if (event.defaultPrevented || event.repeat) {
            return;
        }

        if ((event as any)[Hotkey.handledFlag]) {
            return;
        }

        if (!this.value) {
            return;
        }

        if (isKeyboardEventFromTextInput(event)) {
            return;
        }

        const expected = normalizeHotkeyString(this.value);
        if (!expected) {
            return;
        }

        const pressed = hotkeyFromKeyboardEvent(event);
        if (!pressed || pressed !== expected) {
            return;
        }

        (event as any)[Hotkey.handledFlag] = true;
        this.hotkeyTrigger.emit({
            hotkey: expected,
            value: this.value,
            keyboardEvent: event,
        });
    }
}
